<template>
  <div>
    <h1 class="header"> </h1>
    <div class="form--wrap">
      <p>
                          .           .         ,     .     ,      ,       ( ,  ,  ,   ,       ..). </p>
      <p>
         ,      ()           .       .</p>
    </div>

    <form>
      <transport></transport>
      <reg-and-exp></reg-and-exp>
      <extra-info></extra-info>

      <div class="form--submit">
        <input @click.prevent="$emit('submit')"
               :disabled="errorsLength > 0"
               class="form--send"
               type="submit"
               value="">
        <span class="form--error" v-if="errorsLength > 0">  </span>
      </div>
    </form>
  </div>
</template>

<script>
  import Transport from "./CalcForm/Transport";
  import RegAndExp from "./CalcForm/RegAndExp";
  import ExtraInfo from "./CalcForm/ExtraInfo";
  import {mapGetters} from "vuex";

  export default {
    props: ["showCompaniesList"],

    computed: {
      ...mapGetters("errors", {
        "errorsLength": "errorsLength"
      }),
      ...mapGetters([
        "transportData"
      ])
    },

    components: {
      Transport,
      RegAndExp,
      ExtraInfo
    }
  }
</script>





